import axios from '@axios'

const url = 'http://localhost:2999/api/boxes/'
const EntityModelID = 'code' // default is "_id"

export default {
  namespaced: true,
  state: {
    boxes: [],
    indexedBoxes: [],
  },
  getters: {
    GET: state => state,
  },
  mutations: {
    SET(state, val) {
      state.boxes = val;

      for (var box of state.boxes) {
        if ("key1" in box && box.key1.length > 0) state.indexedBoxes[box.key1] = box;
        if ("key2" in box && box.key2.length > 0) state.indexedBoxes[box.key2] = box;
        /*if ("key3" in box && box.key3.length > 0) state.indexedBoxes[box.key3] = box;
        if ("key4" in box && box.key4.length > 0) state.indexedBoxes[box.key4] = box;*/
      }

      console.log("Indexed by key boxes are:", state.indexedBoxes);
    },
  },
  actions: {
    getBy(ctx, params) {
      console.log('getBy', params)
      return new Promise((resolve, reject) => {
        axios
          .get(url, {
            params: {
              field: params.field,
              value: params.value,
            },
          })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    get(ctx) {
      return new Promise((resolve, reject) => {
        axios.get(url)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    update(ctx, params) {
      return new Promise((resolve, reject) => {
        axios
          .put(`${url}${params[EntityModelID]}`, params)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
  },
}
