import Vue from 'vue'
import { ToastPlugin, ModalPlugin } from 'bootstrap-vue'
import VueCompositionAPI from '@vue/composition-api'
import VueSweetalert2 from 'vue-sweetalert2'

import CountryFlag from 'vue-country-flag' // https://www.npmjs.com/package/vue-country-flag
import router from './router'
import store from './store'
import App from './App.vue'

// Global Components
import './global-components'

// 3rd party plugins
import '@/libs/portal-vue'
import '@/libs/toastification'

import { keyboardHook, keyboardInputCheckTimer } from './utils'

// BSV Plugin Registration
Vue.use(ToastPlugin)
Vue.use(ModalPlugin)

// Composition API
Vue.use(VueCompositionAPI)

// VueSweetalert2
Vue.use(VueSweetalert2)

// CountryFlag
Vue.component('country-flag', CountryFlag)

// import core styles
require('@core/scss/core.scss')

// import assets styles
require('@/assets/scss/style.scss')

// Form wizard requires font icon version of feather icons.
require('@core/assets/fonts/feather/iconfont.css')

Vue.config.productionTip = false;

window.$eventBus = new Vue();

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')

store.dispatch('countries/getIndexed').then(result => {
  store.commit('countries/SET', result.data.data)
})
store.dispatch('goods/getIndexed').then(result => {
  store.commit('goods/SET', result.data.data)
})
store.dispatch('goodcategories/getIndexed').then(result => {
  store.commit('goodcategories/SET', result.data.data)
})
store.dispatch('boxes/get').then(result => {
  //console.log(result.data.data);
  store.commit('boxes/SET', result.data.data); //this will also process indexed boxes
})

keyboardHook().start();
keyboardInputCheckTimer().start();
