import $store from '@/store'
import vue from 'vue'; //for eventbus

export const elementAt = function (table, id, field) {
  // console.log($store.getters[table+"/GET"])
  // return {table, id, field};
  let val = ''
  try {
    val = $store.getters[`${table}/GET`][id][field]
  } catch (err) {
    // not existing
  }
  return val
}

export const boxLocationToReadable = function (location) {
  if (location.toLowerCase() == 'unknown') return 'esterno'
  if (location.toLowerCase() == 'stall') return 'stallo'
  if (location.toLowerCase() == 'depot') return 'magazzino'
  return '/'
}

export const keyboardInputCheckTimer = function () {

  var ret = {};
  var _vars = {};

  ret.start = function () {
    _vars.timer = window.setInterval(function () {
      verifyEventNature();
    }, 10);
  }

  ret.stop = function () {
    clearInterval(_vars.timer);
  }

  return ret;
}


export const keyboardHook = function () {
  var ret = {};
  var _hook = {};

  _hook.listenKeyboard = function (event) {
    var key = event.key;
    var code = event.code;
    //console.log(`ðŸ‘‡ Key down ${key}, ${code}`, event);

    $store.commit("defaults/addToKeyQueue", key);

    _hook.stopPropagation(event);
  }

  _hook.lose = function (event) {
    _hook.stopPropagation(event);
  }

  _hook.stopPropagation = function (event) {
    if ($store.getters[`defaults/GET`]["keyHookEnabled"]) {
      if (event) {
        if (typeof event.cancelBubble === 'boolean') { event.cancelBubble = true; }
        if (event.stopPropagation) event.stopPropagation();
        event.preventDefault();
      }
      return false;
    }
    return true;
  }

  /*_hook.keypress = function (event) {
    var key = event.key;
    var code = event.code;
    console.log(`Key pressed ${key}, ${code}`);
  }

  _hook.keyup = function (event) {
    var key = event.key;
    var code = event.code;
    console.log(`Key up ${key}, ${code}`);
  }*/

  ret.start = function () {
    document.addEventListener('keydown', _hook.listenKeyboard, true); //true to use useCapture
    document.addEventListener('keypress', _hook.lose, true); //true to use useCapture
    document.addEventListener('keyup', _hook.lose, true); //true to use useCapture
  }

  ret.stop = function () {

  }


  return ret;
}

export const verifyEventNature = function () {

  var keyDelta = $store.getters[`defaults/GET`]["keyDelta"];
  var queue = $store.getters[`defaults/GET`]["keyQueue"];

  var now = performance.now();

  var remQueue = ""; //remaining queue

  //console.log("ðŸ”Ž Verifying queue:", queue);
  if (queue.length == 0) {
    return;
  }
  else if(isBox(queue.map(item => item.key).join("")))
  {
    remQueue = queue.map(item => item.key).join("");
    var box = isBox(remQueue);
    if (box) {
      dispatchEvent("box", box);
      return;
    }
    
  }
  else if (queue.at(-1).key == "Enter" || queue.at(-1).key == "&" ) {
    queue.pop();
    remQueue = queue.map(item => item.key).join("");

    //console.log(remQueue);

    var weight = isWeightScale(remQueue);
    console.log("weight is" + weight);
    if (weight) {
      dispatchEvent("weight", weight);
      return;
    }

    var box = isBox(remQueue);
    if (box) {
      dispatchEvent("box", box);
      return;
    }

    /**/

  }

  if ((now - queue[queue.length - 1].time) > keyDelta) {
    remQueue = queue.map(item => item.key).join("");
    dispatchEvent("key", remQueue);
  }

}

window.dispevt = function (type, content) {
  window.$eventBus.$emit(type, content);
}

const dispatchEvent = function (type, content) {
  if (type == "generic") {
    console.log("ðŸ¹ Dispatch GENERIC:", content)
  }
  else {
    $store.commit("defaults/clearKeyQueue");

    if (type == "key") {
      console.log("âŒ¨ Dispatch KEY:", content, document.activeElement);
      document.activeElement.value += content;
      triggerChange(document.activeElement);
    }
    else if (type == "box") {
      console.log("ðŸ—³ Dispatch BOX:", content.code, content);
      /*var activeElement = getVisibleAndListeningElement("box");
      activeElement.value = content.code;
      triggerChange(activeElement);*/
      console.log("ðŸ—£ send", content);
      window.$eventBus.$emit('box', content);

      content.location = "stall";
      content.box_code = content.code;

      $store.dispatch("flows/add", content).then((result) => {
        console.log("- flow added", result);
      });

    }
    else if (type == "weight") {
      console.log("ðŸ‹ Dispatch WEIGHT:", content);

      console.log("ðŸ—£ send", content);
      window.$eventBus.$emit('weight', content);
    }
  }
}

