const triggerChange = function (element) {

  if ("createEvent" in document) {
    var evt = document.createEvent("HTMLEvents");
    evt.initEvent("input", false, true);
    element.dispatchEvent(evt);
  }
  else
    element.fireEvent("onchange");
}

const isBox = function (key) {
  var boxList = $store.getters[`boxes/GET`]["indexedBoxes"];
  
  var cleanedKey = key.replaceAll("Shift","");
  console.log(cleanedKey, boxList);
  if (cleanedKey in boxList) {
    return boxList[cleanedKey];
  }

  return false;
}

const isWeightScale = function (content) {
  console.log(content, content.includes("kgShift"))
  if (content.includes("kgShift"))
    return content.replace("kgShift", "");
  else
    return false;
}