import axios from '@axios'

const url = 'http://localhost:2999/api/sales/'

export default {
  namespaced: true,
  state: {
    boxes: '',
  },
  getters: {},
  mutations: {
    SET(state, val) {
      state.sales = val
    },
  },
  actions: {
    get(ctx) {
      return new Promise((resolve, reject) => {
        axios.get(url)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    getLatest(ctx) {
      return new Promise((resolve, reject) => {
        axios.get(url+"getLatest")
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    add(ctx, params) {
      console.log(params)
      return new Promise((resolve, reject) => {
        axios
          .post(url, params)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
  },
}
