import axios from '@axios'

const url = 'http://localhost:2999/api/goods/'

export default {
  namespaced: true,
  state: {
    goods: '',
  },
  getters: {
    GET: state => state.goods,
  },
  mutations: {
    SET(state, val) {
      state.goods = val
    },
  },
  actions: {

    get(ctx) {
      return new Promise((resolve, reject) => {
        axios.get(url)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },

    getIndexed(ctx) {
      return new Promise((resolve, reject) => {
        axios.get(`${url}indexed`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },

  },
}
