export default {
  namespaced: true,
  state: {
    goodLocationAtCheckin: 'stall',
    boxTare: 0.1,
    keyDelta: 150,
    keyHookEnabled: true,
    keyQueue: [],
  }, 
  mutations: {
    addToKeyQueue(state, val){
      var newKey = {
        key: val,
        time: performance.now()
      }
      state.keyQueue.push(newKey);      
    },
    clearKeyQueue(state){
      state.keyQueue = [];
    },
    setKeyHookEnabled(state,val){
      console.log("setting keyHookEnabled to", val)
      state.keyHookEnabled = val;
    },
  },
  getters: {
    GET: state => state,
  },
}